html{font-size:62.5%}body{background:#333745;font-family:Helvetica, Ariaal, sans-serif;font-size:1.6rem}button{cursor:pointer}button:disabled{cursor:default}.big_button{background:#8357c5;border:none;border-bottom:0.6rem solid #693cad;color:white;font-weight:bold;font-size:3.2rem;width:100%;margin-bottom:4.8rem;padding:2.4rem}.big_button:disabled{opacity:.5}.button{background:#8357c5;color:white;border:none;border-bottom:0.3rem solid #693cad;font-weight:500;padding:1.2rem}.button--link{background:none;padding:0;border:none;color:#a5afd7}@media (max-width: 45rem){.big_button{margin-bottom:1.6rem}}.widget{background:#464b5e;margin-bottom:4.8rem}.widget-header{background:#3c4251;color:#a5afd7;display:flex;justify-content:space-between;padding:1.6rem}.widget-header--title{margin:0rem}.widget__message{border-bottom:1px solid #c8cee7;color:#a5afd7;margin:0rem;padding:3.2rem;text-align:center}.option{border-bottom:1px solid #5c627b;display:flex;justify-content:space-between;padding:3.2rem 1.6rem}.option__text{margin:0rem;color:white;font-size:2rem;font-weight:500;word-break:break-all}.add-option-error{color:#a5afd7;font-style:italic;padding:0rem 1.6rem;margin:1.6rem 0 0 0}.add-option{display:flex;flex-direction:row;padding:1.6rem}.add-option__input{background:#333745;color:#a5afd7;padding:1.2rem;border:none;border-bottom:0.3rem solid #1d2028;margin-right:1.2rem;flex-grow:1}@media (max-width: 45rem){.add-option{flex-direction:column}.add-option__input{margin:0 0 1.2rem 0}}.container{max-width:60rem;margin:0rem auto;padding:0rem 1.6rem}.header{background:#20222b;margin-bottom:4.8rem;padding:1.6rem 0rem;color:white}.header__title{font-size:3.2rem;margin:0rem}.header__subtitle{color:#a5afd7;font-size:1.6rem;margin:0rem;font-weight:500}@media (max-width: 45rem){.header{margin-bottom:1.6rem}}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{display:flex;justify-content:center;align-items:center;transition:opacity 200ms ease-in-out}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:1}.modal{background:#464b5e;color:white;max-width:30rem;outline:none;padding:3.2rem;text-align:center}.modal__title{margin:0 0 1.6rem 0}.modal__body{font-size:2rem;font-weight:300;margin:0 0 3.2rem 0;word-break:break-all}
